@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 2% 5%;
  background-color: #e4e1e8;
  background-image: linear-gradient(#e4e1e8, lightgray);
  font-family: "Raleway", sans-serif;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

a {
  color: mediumblue;
  font-size: 16px;
  text-decoration: none;
}

a:hover {
  color: darkblue;
  font-weight: 600;
}

a:visited {
  color: darkgreen;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: relative;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.event {
  background-color: #fff;
  padding: 10px 10px 35px;
  margin-top: 40px;
  margin-bottom: 0px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.details-button {
  background: #fff;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 0.375rem;
  border-style: solid;
  border-width: 0.125rem;
  box-sizing: border-box;
  color: #212121;
  cursor: pointer;
  display: inline-block;
  font-family: Circular, Helvetica, sans-serif;
  font-size: 1.125rem;
  font-weight: 700;
  letter-spacing: -0.01em;
  line-height: 1.3;
  padding: 0.875rem 1.125rem;
  position: static;
  text-align: left;
  text-decoration: none;
  -webkit-transform: translateZ(0) scale(1);
          transform: translateZ(0) scale(1);
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  -ms-user-select: none;
      user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.details-button:not(:disabled):hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.details-button:not(:disabled):hover:active {
  -webkit-transform: scale(1.05) translateY(0.125rem);
          transform: scale(1.05) translateY(0.125rem);
}

.details-button:focus {
  outline: 0 solid transparent;
}

.details-button:focus:before {
  content: "";
  left: calc(-1 * 0.375rem);
  pointer-events: none;
  position: absolute;
  top: calc(-1 * 0.375rem);
  transition: border-radius;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.details-button:focus:not(:focus-visible) {
  outline: 0 solid transparent;
}

.details-button:focus:not(:focus-visible):before {
  border-width: 0;
}

.details-button:not(:disabled):active {
  -webkit-transform: translateY(0.125rem);
          transform: translateY(0.125rem);
}

.summary {
  font-size: 28px;
  font-weight: 700;
}

.start-date {
  font-size: 18px;
  font-style: italic;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 80px;
  margin: 20px auto;
  text-align: center;
}

